import React, { useState, useEffect } from 'react';
import styles from './MedicationAutocomplete.module.css';

/**
 * MedicationAutocomplete - Medication name input with dictionary suggestions
 *
 * @param {string} value - Current medication name
 * @param {Function} onChange - Callback(value) when name changes
 * @param {Function} onSelectDosage - Callback(dosage) when dosage chip clicked
 * @param {Function} onSelectSchedule - Callback(schedule) when schedule chip clicked
 */
export default function MedicationAutocomplete({
  value,
  onChange,
  onSelectDosage,
  onSelectSchedule
}) {
  const [medications, setMedications] = useState([]);
  const [suggestions, setSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [selectedMed, setSelectedMed] = useState(null);
  const [loading, setLoading] = useState(true);

  // Load medications dictionary on mount
  useEffect(() => {
    fetch('/data/medications-dictionary.json')
      .then((res) => res.json())
      .then((data) => {
        setMedications(data.medications || []);
        setLoading(false);
      })
      .catch((err) => {
        console.error('Failed to load medications dictionary:', err);
        setLoading(false);
      });
  }, []);

  // Filter suggestions based on input
  useEffect(() => {
    if (!value || value.length < 2) {
      setSuggestions([]);
      setShowSuggestions(false);
      setSelectedMed(null);
      return;
    }

    const query = value.toLowerCase();
    const matches = medications
      .filter((med) => {
        // Match generic name
        if (med.genericName.toLowerCase().includes(query)) return true;
        // Match any brand name
        if (med.brandNames.some((brand) => brand.toLowerCase().includes(query))) return true;
        return false;
      })
      .slice(0, 10); // Limit to 10 suggestions

    setSuggestions(matches);
    setShowSuggestions(matches.length > 0);
  }, [value, medications]);

  const handleSelectMedication = (med) => {
    onChange(med.genericName);
    setSelectedMed(med);
    setShowSuggestions(false);
  };

  const handleBlur = () => {
    // Delay to allow click on suggestions
    setTimeout(() => setShowSuggestions(false), 200);
  };

  const handleFocus = () => {
    if (value.length >= 2 && suggestions.length > 0) {
      setShowSuggestions(true);
    }
  };

  return (
    <div className={styles.container}>
      <label className={styles.field}>
        <span className={styles.label}>Medication Name</span>
        <input
          type="text"
          className={styles.input}
          value={value}
          onChange={(e) => onChange(e.target.value)}
          onBlur={handleBlur}
          onFocus={handleFocus}
          placeholder={loading ? 'Loading...' : 'Start typing medication name...'}
          disabled={loading}
        />
      </label>

      {/* Suggestions dropdown */}
      {showSuggestions && (
        <div className={styles.dropdown}>
          {suggestions.map((med) => (
            <button
              key={med.id}
              type="button"
              className={styles.suggestion}
              onClick={() => handleSelectMedication(med)}
            >
              <strong>{med.genericName}</strong>
              {med.brandNames.length > 0 && (
                <span className={styles.brands}>
                  ({med.brandNames.slice(0, 2).join(', ')})
                </span>
              )}
            </button>
          ))}
        </div>
      )}

      {/* Helper chips - show after medication selected */}
      {selectedMed && (
        <div className={styles.helpers}>
          {selectedMed.commonDosages.length > 0 && (
            <div className={styles.helperSection}>
              <span className={styles.helperLabel}>Common dosages:</span>
              <div className={styles.chips}>
                {selectedMed.commonDosages.map((dosage) => (
                  <button
                    key={dosage}
                    type="button"
                    className={styles.chip}
                    onClick={() => onSelectDosage && onSelectDosage(dosage)}
                  >
                    {dosage}
                  </button>
                ))}
              </div>
            </div>
          )}

          {selectedMed.commonSchedules.length > 0 && (
            <div className={styles.helperSection}>
              <span className={styles.helperLabel}>Common schedules:</span>
              <div className={styles.chips}>
                {selectedMed.commonSchedules.map((schedule) => (
                  <button
                    key={schedule}
                    type="button"
                    className={styles.chip}
                    onClick={() => onSelectSchedule && onSelectSchedule(schedule)}
                  >
                    {schedule}
                  </button>
                ))}
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
