/**
 * Data helper utilities
 * Functions for validating, filtering, and manipulating medical form data
 */

/**
 * Check if a value is a non-empty string (after trimming)
 * @param {*} str - Value to check
 * @returns {boolean} True if string is non-empty after trimming
 */
export function isNonEmptyString(str) {
  return !!(str && String(str).trim() !== '');
}

/**
 * Filter array rows to only include those with at least one non-empty field
 * @param {Array} arr - Array of objects to filter
 * @param {Array<string>} keys - Keys to check for non-empty values
 * @returns {Array} Filtered array containing only rows with data
 */
export function filterNonEmptyRows(arr, keys) {
  if (!Array.isArray(arr)) return [];
  return arr.filter((row) =>
    keys.some((k) => isNonEmptyString(row && row[k]))
  );
}

/**
 * Ensure array field exists and has correct default template
 * Returns default empty array if field doesn't exist
 * @param {string} field - Field name
 * @returns {Array} Array with default template
 */
export function ensureArrayField(field) {
  switch (field) {
    case 'diagnoses':
      return [{ name: '', by: '', date: '', status: '' }];
    case 'hospitalizations':
      return [{ why: '', when: '' }];
    case 'procedures':
      return [{ name: '', date: '', notes: '' }];
    case 'treatments':
      return [{ name: '', timeframe: '', effectiveness: '', sideEffects: '' }];
    case 'testsImaging':
      return [{ test: '', finding: '', date: '' }];
    case 'meds':
    case 'medications':
      return [{ name: '', dose: '', freq: '' }];
    case 'allergies':
      return [{ item: '', reaction: '' }];
    case 'doctors':
      return [{ name: '', specialty: '', contact: '' }];
    default:
      return [{}];
  }
}

/**
 * Get default template for a specific field type
 * @param {string} field - Field name
 * @returns {Object} Empty template object
 */
export function getEmptyTemplate(field) {
  const templates = ensureArrayField(field);
  return templates[0] || {};
}

/**
 * Update a specific item in an array
 * @param {Array} arr - Source array
 * @param {number} index - Index to update
 * @param {string} key - Property key to update
 * @param {*} value - New value
 * @returns {Array} New array with updated item
 */
export function updateArrayItem(arr, index, key, value) {
  const newArr = [...arr];
  if (!newArr[index]) newArr[index] = {};
  newArr[index] = { ...newArr[index], [key]: value };
  return newArr;
}

/**
 * Add a new item to an array
 * @param {Array} arr - Source array
 * @param {Object} template - Template object for new item
 * @returns {Array} New array with added item
 */
export function addArrayItem(arr, template) {
  return [...arr, template];
}

/**
 * Remove an item from an array
 * Keeps at least one item (using fallback template if array would be empty)
 * @param {Array} arr - Source array
 * @param {number} index - Index to remove
 * @param {Object} fallbackTemplate - Template to use if array would be empty
 * @returns {Array} New array with item removed
 */
export function removeArrayItem(arr, index, fallbackTemplate) {
  const newArr = [...arr];
  newArr.splice(index, 1);
  if (newArr.length === 0) {
    newArr.push(fallbackTemplate);
  }
  return newArr;
}

/**
 * Parse a comma-separated string into an array of trimmed values
 * @param {string} str - Comma-separated string
 * @returns {Array<string>} Array of trimmed non-empty strings
 */
export function parseCommaSeparated(str) {
  if (!str || typeof str !== 'string') return [];
  return str
    .split(',')
    .map((s) => s.trim())
    .filter(Boolean);
}

/**
 * Merge arrays ensuring no duplicates
 * @param {Array} arr1 - First array
 * @param {Array} arr2 - Second array
 * @returns {Array} Merged array without duplicates
 */
export function mergeArraysUnique(arr1, arr2) {
  return [...new Set([...(arr1 || []), ...(arr2 || [])])];
}
