/**
 * Abstract storage layer
 * Supports both localStorage (guest mode) and Firebase (authenticated)
 * All functions are async for Firebase compatibility
 */

import { doc, getDoc, setDoc, deleteDoc, serverTimestamp } from 'firebase/firestore';
import { db, auth, isFirebaseConfigured } from './firebase';
import {
  loadFromLocalStorage,
  saveToLocalStorage,
  removeFromLocalStorage,
  hasLocalStorageKey
} from '../utils/localStorage';

// Storage keys for localStorage
export const StorageKeys = {
  PATIENT_PERMANENT: 'patientPermanent',
  PATIENT_VISIT: 'patientVisit',
  INTRO_SEEN: 'intro_seen_v1'
};

/**
 * Check if user is authenticated with Firebase
 */
function isAuthenticated() {
  return isFirebaseConfigured() && auth?.currentUser !== null;
}

/**
 * Get current user ID
 */
function getUserId() {
  return auth?.currentUser?.uid;
}

/**
 * Get Firestore document reference for current user's patient data
 */
function getPatientDocRef() {
  const userId = getUserId();
  if (!userId || !db) return null;
  return doc(db, 'users', userId, 'patients', 'current');
}

// ============================================
// LOAD FUNCTIONS
// ============================================

/**
 * Load all patient data (permanent + visit)
 * Uses Firestore if authenticated, localStorage otherwise
 * @returns {Promise<{permanent: Object, visit: Object}>}
 */
export async function loadPatientData() {
  if (isAuthenticated()) {
    return loadFromFirestore();
  }
  return loadFromLocal();
}

/**
 * Load from localStorage (guest mode)
 */
function loadFromLocal() {
  return {
    permanent: loadFromLocalStorage(StorageKeys.PATIENT_PERMANENT, {}),
    visit: loadFromLocalStorage(StorageKeys.PATIENT_VISIT, {})
  };
}

/**
 * Load from Firestore (authenticated)
 */
async function loadFromFirestore() {
  const docRef = getPatientDocRef();
  if (!docRef) return { permanent: {}, visit: {} };

  try {
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      const data = docSnap.data();
      return {
        permanent: data.permanent || {},
        visit: data.visit || {}
      };
    }
    return { permanent: {}, visit: {} };
  } catch (err) {
    console.error('Failed to load from Firestore:', err);
    // Fallback to localStorage
    return loadFromLocal();
  }
}

// ============================================
// SAVE FUNCTIONS
// ============================================

/**
 * Save patient permanent data (name, DOB, diagnoses, etc.)
 * @param {Object} data - Patient permanent data
 * @returns {Promise<Object>} The saved data
 */
export async function savePatientPermanent(data) {
  if (isAuthenticated()) {
    await saveToFirestore({ permanent: data });
  } else {
    saveToLocalStorage(StorageKeys.PATIENT_PERMANENT, data);
  }
  return data;
}

/**
 * Save patient visit data (symptoms, current meds)
 * @param {Object} data - Patient visit data
 * @returns {Promise<Object>} The saved data
 */
export async function savePatientVisit(data) {
  if (isAuthenticated()) {
    await saveToFirestore({ visit: data });
  } else {
    saveToLocalStorage(StorageKeys.PATIENT_VISIT, data);
  }
  return data;
}

/**
 * Save to Firestore (merge with existing data)
 */
async function saveToFirestore(data) {
  const docRef = getPatientDocRef();
  if (!docRef) return;

  try {
    await setDoc(docRef, {
      ...data,
      updatedAt: serverTimestamp()
    }, { merge: true });
  } catch (err) {
    console.error('Failed to save to Firestore:', err);
    throw err;
  }
}

// ============================================
// CLEAR FUNCTIONS
// ============================================

/**
 * Clear all patient data (both permanent and visit)
 * @returns {Promise<void>}
 */
export async function clearPatientData() {
  if (isAuthenticated()) {
    await clearFirestoreData();
  }
  // Always clear localStorage too
  removeFromLocalStorage([
    StorageKeys.PATIENT_PERMANENT,
    StorageKeys.PATIENT_VISIT
  ]);
}

/**
 * Clear Firestore patient data
 */
async function clearFirestoreData() {
  const docRef = getPatientDocRef();
  if (!docRef) return;

  try {
    await deleteDoc(docRef);
  } catch (err) {
    console.error('Failed to clear Firestore data:', err);
    throw err;
  }
}

// ============================================
// INTRO/ONBOARDING
// ============================================

/**
 * Check if user has seen the intro screen
 * @returns {Promise<boolean>}
 */
export async function hasSeenIntro() {
  return hasLocalStorageKey(StorageKeys.INTRO_SEEN);
}

/**
 * Mark intro as seen
 * @returns {Promise<void>}
 */
export async function markIntroSeen() {
  saveToLocalStorage(StorageKeys.INTRO_SEEN, 'true');
}

// ============================================
// MIGRATION FUNCTIONS
// ============================================

/**
 * Check if there's existing localStorage data to migrate
 * Used when user signs up to offer migration
 * @returns {Promise<boolean>}
 */
export async function hasLocalData() {
  const { permanent, visit } = loadFromLocal();
  const hasPermanent = Object.keys(permanent).length > 0;
  const hasVisit = Object.keys(visit).length > 0;
  return hasPermanent || hasVisit;
}

/**
 * Get raw localStorage data for migration to cloud
 * @returns {Promise<{permanent: Object, visit: Object}>}
 */
export async function getLocalDataForMigration() {
  return loadFromLocal();
}

/**
 * Migrate localStorage data to Firestore
 * Called after user signs up/signs in
 * @returns {Promise<boolean>} True if migration occurred
 */
export async function migrateLocalToCloud() {
  if (!isAuthenticated()) return false;

  const hasData = await hasLocalData();
  if (!hasData) return false;

  try {
    const { permanent, visit } = loadFromLocal();
    const docRef = getPatientDocRef();

    if (docRef) {
      await setDoc(docRef, {
        permanent,
        visit,
        migratedAt: serverTimestamp(),
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp()
      });

      // Clear localStorage after successful migration
      removeFromLocalStorage([
        StorageKeys.PATIENT_PERMANENT,
        StorageKeys.PATIENT_VISIT
      ]);

      return true;
    }
    return false;
  } catch (err) {
    console.error('Migration failed:', err);
    throw err;
  }
}

/**
 * Clear local data after successful migration to cloud
 * @returns {Promise<void>}
 */
export async function clearLocalDataAfterMigration() {
  removeFromLocalStorage([
    StorageKeys.PATIENT_PERMANENT,
    StorageKeys.PATIENT_VISIT
  ]);
}
