import React from 'react';
import styles from './NavigationHeader.module.css';
import { useIsMobile } from '../hooks/useIsMobile';

/**
 * NavigationHeader - Progress indicator showing current screen position
 *
 * @param {number} screenNumber - Current screen number (1-indexed)
 * @param {number} totalScreens - Total number of screens
 * @param {string} screenName - Display name of current screen
 */
export default function NavigationHeader({ screenNumber, totalScreens, screenName }) {
  const isMobile = useIsMobile();

  // Calculate progress percentage
  const progressPercent = Math.round((screenNumber / totalScreens) * 100);

  return (
    <div className={styles.header}>
      <div className={styles.textRow}>
        <span className={styles.screenCounter}>
          Screen {screenNumber} of {totalScreens}
        </span>
      </div>

      {/* Progress bar - show on tablet/desktop, optional on mobile */}
      {!isMobile && (
        <div className={styles.progressBarContainer}>
          <div
            className={styles.progressBarFill}
            style={{ width: `${progressPercent}%` }}
            role="progressbar"
            aria-valuenow={progressPercent}
            aria-valuemin="0"
            aria-valuemax="100"
            aria-label={`Progress: ${progressPercent}%`}
          />
        </div>
      )}

      {/* Progress dots - alternative visual for mobile */}
      {isMobile && (
        <div className={styles.progressDots}>
          {Array.from({ length: totalScreens }).map((_, index) => (
            <div
              key={index}
              className={`${styles.dot} ${
                index + 1 === screenNumber ? styles.dotActive : ''
              } ${index + 1 < screenNumber ? styles.dotCompleted : ''}`}
              aria-label={`Screen ${index + 1}${index + 1 === screenNumber ? ' (current)' : ''}`}
            />
          ))}
        </div>
      )}
    </div>
  );
}
