import React from 'react';
import ScreenHeader from '../components/ScreenHeader';
import MedicationAutocomplete from '../components/MedicationAutocomplete';
import { ensureArrayField, updateArrayItem, addArrayItem, removeArrayItem, getEmptyTemplate } from '../utils/dataHelpers';
import styles from './MedicationsScreen.module.css';

/**
 * Screen 4: Medications & Allergies
 * Current medications with dictionary autocomplete + allergies
 */
export default function MedicationsScreen({ patientData, visitData, updatePatient, updateVisit }) {
  // Medications from visit data
  const meds = Array.isArray(visitData?.meds)
    ? visitData.meds
    : ensureArrayField('meds');

  // Allergies from patient data
  const allergies = Array.isArray(patientData?.allergies)
    ? patientData.allergies
    : ensureArrayField('allergies');

  return (
    <div className={styles.screen}>
      <ScreenHeader
        title="Medications & Allergies"
        subtitle="Current medications and known allergies"
      />

      {/* Medications Section */}
      <section className={styles.section}>
        <h3 className={styles.sectionTitle}>Current Medications</h3>

        {meds.map((med, index) => (
          <div key={index} className={styles.medCard}>
            <div className={styles.cardHeader}>
              <span className={styles.medNumber}>Medication {index + 1}</span>
              <button
                type="button"
                className={styles.deleteButton}
                onClick={() => {
                  const updated = removeArrayItem(meds, index, getEmptyTemplate('meds'));
                  updateVisit({ meds: updated });
                }}
              >
                Remove
              </button>
            </div>

            <MedicationAutocomplete
              value={med.name || ''}
              onChange={(value) => {
                const updated = updateArrayItem(meds, index, 'name', value);
                updateVisit({ meds: updated });
              }}
              onSelectDosage={(dosage) => {
                const updated = updateArrayItem(meds, index, 'dose', dosage);
                updateVisit({ meds: updated });
              }}
              onSelectSchedule={(schedule) => {
                const updated = updateArrayItem(meds, index, 'freq', schedule);
                updateVisit({ meds: updated });
              }}
            />

            <div className={styles.row}>
              <label className={styles.field}>
                <span className={styles.label}>Dose</span>
                <input
                  type="text"
                  className={styles.input}
                  value={med.dose || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(meds, index, 'dose', e.target.value);
                    updateVisit({ meds: updated });
                  }}
                  placeholder="500 mg"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>How Often</span>
                <input
                  type="text"
                  className={styles.input}
                  value={med.freq || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(meds, index, 'freq', e.target.value);
                    updateVisit({ meds: updated });
                  }}
                  placeholder="Twice daily"
                />
              </label>
            </div>
          </div>
        ))}

        <button
          type="button"
          className={styles.addButton}
          onClick={() => {
            const updated = addArrayItem(meds, getEmptyTemplate('meds'));
            updateVisit({ meds: updated });
          }}
        >
          + Add Medication
        </button>
      </section>

      {/* Allergies Section */}
      <section className={styles.section}>
        <h3 className={styles.sectionTitle}>Allergies</h3>

        {allergies.map((allergy, index) => (
          <div key={index} className={styles.allergyCard}>
            <div className={styles.cardHeader}>
              <span className={styles.medNumber}>Allergy {index + 1}</span>
              <button
                type="button"
                className={styles.deleteButton}
                onClick={() => {
                  const updated = removeArrayItem(allergies, index, getEmptyTemplate('allergies'));
                  updatePatient({ allergies: updated });
                }}
              >
                Remove
              </button>
            </div>

            <label className={styles.field}>
              <span className={styles.label}>Allergen</span>
              <input
                type="text"
                className={styles.input}
                value={allergy.item || ''}
                onChange={(e) => {
                  const updated = updateArrayItem(allergies, index, 'item', e.target.value);
                  updatePatient({ allergies: updated });
                }}
                placeholder="Penicillin"
              />
            </label>

            <label className={styles.field}>
              <span className={styles.label}>Reaction</span>
              <input
                type="text"
                className={styles.input}
                value={allergy.reaction || ''}
                onChange={(e) => {
                  const updated = updateArrayItem(allergies, index, 'reaction', e.target.value);
                  updatePatient({ allergies: updated });
                }}
                placeholder="Severe rash and swelling"
              />
            </label>
          </div>
        ))}

        <button
          type="button"
          className={styles.addButton}
          onClick={() => {
            const updated = addArrayItem(allergies, getEmptyTemplate('allergies'));
            updatePatient({ allergies: updated });
          }}
        >
          + Add Allergy
        </button>
      </section>
    </div>
  );
}
