/**
 * localStorage utility functions
 * Provides safe read/write operations with error handling
 */

/**
 * Load data from localStorage with JSON parsing
 * @param {string} key - localStorage key
 * @param {*} defaultValue - Default value if key doesn't exist or parsing fails
 * @returns {*} Parsed value or defaultValue
 */
export function loadFromLocalStorage(key, defaultValue = {}) {
  try {
    const raw = localStorage.getItem(key);
    if (raw === null) {
      return defaultValue;
    }
    return JSON.parse(raw);
  } catch (err) {
    console.error(`Failed to load ${key} from localStorage:`, err);
    return defaultValue;
  }
}

/**
 * Save data to localStorage with JSON stringification
 * @param {string} key - localStorage key
 * @param {*} value - Value to save (will be JSON stringified)
 * @returns {boolean} True if successful, false otherwise
 */
export function saveToLocalStorage(key, value) {
  try {
    localStorage.setItem(key, JSON.stringify(value));
    return true;
  } catch (err) {
    console.error(`Failed to save ${key} to localStorage:`, err);
    return false;
  }
}

/**
 * Remove one or more keys from localStorage
 * @param {string|string[]} keys - Single key or array of keys to remove
 */
export function removeFromLocalStorage(keys) {
  const keyArray = Array.isArray(keys) ? keys : [keys];
  keyArray.forEach((key) => {
    try {
      localStorage.removeItem(key);
    } catch (err) {
      console.error(`Failed to remove ${key} from localStorage:`, err);
    }
  });
}

/**
 * Check if a key exists in localStorage
 * @param {string} key - localStorage key
 * @returns {boolean} True if key exists
 */
export function hasLocalStorageKey(key) {
  try {
    return localStorage.getItem(key) !== null;
  } catch (err) {
    console.error(`Failed to check ${key} in localStorage:`, err);
    return false;
  }
}

/**
 * Clear all localStorage (use with caution!)
 */
export function clearAllLocalStorage() {
  try {
    localStorage.clear();
    return true;
  } catch (err) {
    console.error('Failed to clear localStorage:', err);
    return false;
  }
}
