import { useState, useEffect, useCallback } from 'react';
import {
  loadPatientData,
  savePatientPermanent,
  savePatientVisit,
  clearPatientData
} from '../api/storage';

/**
 * usePatientData - Centralized hook for patient data management
 * Handles loading, saving, and clearing patient data
 * Currently uses localStorage via storage API, will use Firebase in Phase 2
 */
export function usePatientData() {
  const [patientData, setPatientData] = useState({});
  const [visitData, setVisitData] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Load data on mount
  useEffect(() => {
    async function load() {
      try {
        setIsLoading(true);
        const { permanent, visit } = await loadPatientData();
        setPatientData(permanent);
        setVisitData(visit);
        setError(null);
      } catch (err) {
        console.error('Failed to load patient data:', err);
        setError(err);
      } finally {
        setIsLoading(false);
      }
    }
    load();
  }, []);

  // Update patient (permanent) data
  const updatePatient = useCallback((updates) => {
    setPatientData(prev => {
      const newData = { ...prev, ...updates };
      // Fire and forget - save in background
      savePatientPermanent(newData).catch(err => {
        console.error('Failed to save patient data:', err);
      });
      return newData;
    });
  }, []);

  // Update visit data
  const updateVisit = useCallback((updates) => {
    setVisitData(prev => {
      const newData = { ...prev, ...updates };
      // Fire and forget - save in background
      savePatientVisit(newData).catch(err => {
        console.error('Failed to save visit data:', err);
      });
      return newData;
    });
  }, []);

  // Clear all data
  const clearAll = useCallback(async () => {
    try {
      await clearPatientData();
      setPatientData({});
      setVisitData({});
      setError(null);
    } catch (err) {
      console.error('Failed to clear patient data:', err);
      setError(err);
      throw err;
    }
  }, []);

  return {
    patientData,
    visitData,
    updatePatient,
    updateVisit,
    clearAll,
    isLoading,
    error
  };
}
