import { useState, useEffect } from 'react';

// Screen constants
export const SCREENS = {
  STORY: 'story',
  DIAGNOSES: 'diagnoses',
  HISTORY: 'history',
  MEDICATIONS: 'medications',
  DOCTORS: 'doctors',
  REVIEW: 'review'
};

// Screen order for navigation
export const SCREEN_ORDER = [
  SCREENS.STORY,
  SCREENS.DIAGNOSES,
  SCREENS.HISTORY,
  SCREENS.MEDICATIONS,
  SCREENS.DOCTORS,
  SCREENS.REVIEW
];

// Screen display names
export const SCREEN_NAMES = {
  [SCREENS.STORY]: 'Your Story',
  [SCREENS.DIAGNOSES]: 'Diagnoses',
  [SCREENS.HISTORY]: 'Medical History',
  [SCREENS.MEDICATIONS]: 'Medications & Allergies',
  [SCREENS.DOCTORS]: 'Doctors & Specialists',
  [SCREENS.REVIEW]: 'Review & Print'
};

/**
 * Custom hook for hash-based screen navigation
 * Manages navigation between the 6 screens using URL hash
 *
 * @returns {Object} Navigation state and functions
 */
export function useScreenNavigation() {
  // Initialize current screen from URL hash, default to first screen
  const [currentScreen, setCurrentScreen] = useState(() => {
    const hash = window.location.hash.slice(1); // Remove '#'
    return SCREEN_ORDER.includes(hash) ? hash : SCREENS.STORY;
  });

  // Listen for hash changes (browser back/forward, direct URL changes)
  useEffect(() => {
    const handleHashChange = () => {
      const hash = window.location.hash.slice(1);
      if (SCREEN_ORDER.includes(hash)) {
        setCurrentScreen(hash);
      }
    };

    window.addEventListener('hashchange', handleHashChange);

    // Set initial hash if not present (only once on mount)
    const currentHash = window.location.hash.slice(1);
    if (!currentHash || !SCREEN_ORDER.includes(currentHash)) {
      // Use replace instead of setting hash to avoid adding to history
      window.location.replace('#' + SCREENS.STORY);
    }

    return () => window.removeEventListener('hashchange', handleHashChange);
  }, []);

  /**
   * Navigate to a specific screen
   * @param {string} screen - Screen ID from SCREENS constant
   */
  const goToScreen = (screen) => {
    if (SCREEN_ORDER.includes(screen)) {
      window.location.hash = screen;
    }
  };

  /**
   * Navigate to the next screen in the flow
   */
  const goNext = () => {
    const currentIndex = SCREEN_ORDER.indexOf(currentScreen);
    if (currentIndex < SCREEN_ORDER.length - 1) {
      goToScreen(SCREEN_ORDER[currentIndex + 1]);
    }
  };

  /**
   * Navigate to the previous screen in the flow
   */
  const goBack = () => {
    const currentIndex = SCREEN_ORDER.indexOf(currentScreen);
    if (currentIndex > 0) {
      goToScreen(SCREEN_ORDER[currentIndex - 1]);
    }
  };

  // Calculate current position
  const screenIndex = SCREEN_ORDER.indexOf(currentScreen);
  const isFirst = screenIndex === 0;
  const isLast = screenIndex === SCREEN_ORDER.length - 1;
  const screenName = SCREEN_NAMES[currentScreen] || '';

  return {
    currentScreen,
    screenName,
    goToScreen,
    goNext,
    goBack,
    isFirst,
    isLast,
    screenIndex,
    screenNumber: screenIndex + 1, // 1-indexed for display
    totalScreens: SCREEN_ORDER.length
  };
}
