import React, { useState } from 'react';
import styles from './DiagnosisCard.module.css';

/**
 * DiagnosisCard - Single diagnosis entry with auto-collapse when complete
 *
 * @param {Object} diagnosis - Diagnosis data {name, by, date, status}
 * @param {number} index - Index in array
 * @param {Function} onUpdate - Callback(field, value) when field changes
 * @param {Function} onDelete - Callback when delete clicked
 */
export default function DiagnosisCard({ diagnosis, index, onUpdate, onDelete }) {
  // Check if diagnosis is complete (all fields filled)
  const isComplete = diagnosis.name && diagnosis.by && diagnosis.date && diagnosis.status;

  // Auto-collapse if complete
  const [isCollapsed, setIsCollapsed] = useState(isComplete);

  const toggleCollapse = () => setIsCollapsed(!isCollapsed);

  return (
    <div className={styles.card}>
      <div className={styles.header} onClick={toggleCollapse}>
        <div className={styles.headerLeft}>
          <span className={styles.icon}>{isCollapsed ? '' : ''}</span>
          <span className={styles.title}>
            {diagnosis.name || `Diagnosis ${index + 1}`}
          </span>
          {isComplete && isCollapsed && (
            <span className={styles.completeBadge}></span>
          )}
        </div>
        <button
          type="button"
          className={styles.deleteButton}
          onClick={(e) => {
            e.stopPropagation();
            onDelete();
          }}
        >
          Remove
        </button>
      </div>

      {!isCollapsed && (
        <div className={styles.content}>
          <label className={styles.field}>
            <span className={styles.label}>Diagnosis Name</span>
            <input
              type="text"
              className={styles.input}
              value={diagnosis.name || ''}
              onChange={(e) => onUpdate('name', e.target.value)}
              placeholder="ME/CFS"
            />
          </label>

          <label className={styles.field}>
            <span className={styles.label}>Diagnosing Clinician</span>
            <input
              type="text"
              className={styles.input}
              value={diagnosis.by || ''}
              onChange={(e) => onUpdate('by', e.target.value)}
              placeholder="Dr. L. Patel, Neurology"
            />
          </label>

          <div className={styles.row}>
            <label className={styles.field}>
              <span className={styles.label}>Date</span>
              <input
                type="text"
                className={styles.input}
                value={diagnosis.date || ''}
                onChange={(e) => onUpdate('date', e.target.value)}
                placeholder="June 2008"
              />
            </label>

            <label className={styles.field}>
              <span className={styles.label}>Status</span>
              <select
                className={styles.select}
                value={diagnosis.status || ''}
                onChange={(e) => onUpdate('status', e.target.value)}
              >
                <option value="">Select...</option>
                <option value="confirmed">Confirmed</option>
                <option value="suspected">Suspected</option>
                <option value="past">Past (resolved)</option>
                <option value="uncertain">Uncertain</option>
              </select>
            </label>
          </div>
        </div>
      )}
    </div>
  );
}
