// src/App.js
import React, { useState, useEffect } from "react";
import IntroScreen from "./IntroScreen";
import MultiScreenView from "./MultiScreenView";
import AuthScreen from "./screens/AuthScreen";
import { useAuth } from "./hooks/useAuth";
import { hasSeenIntro, markIntroSeen, hasLocalData, migrateLocalToCloud } from "./api/storage";

/**
 * App - Main application with optional authentication
 * Flow: Intro  Auth (optional)  Medical Form
 *
 * Auth modes:
 * - Guest: Uses localStorage (no account needed)
 * - Signed in: Uses Firebase (syncs across devices)
 */
export default function App() {
  const {
    user,
    isAuthenticated,
    isLoading: authLoading,
    error: authError,
    firebaseEnabled,
    signIn,
    signUp,
    signInWithGoogle,
    logOut,
    resetPassword,
    clearError
  } = useAuth();

  const [introSeen, setIntroSeen] = useState(null); // null = loading
  const [showAuth, setShowAuth] = useState(false);
  const [isGuestMode, setIsGuestMode] = useState(false);
  const [migrationDone, setMigrationDone] = useState(false);

  // Check if user has seen intro
  useEffect(() => {
    async function checkIntro() {
      const seen = await hasSeenIntro();
      setIntroSeen(seen);
    }
    checkIntro();
  }, []);

  // Handle data migration after sign in
  useEffect(() => {
    async function migrate() {
      if (isAuthenticated && !migrationDone) {
        try {
          const hasData = await hasLocalData();
          if (hasData) {
            await migrateLocalToCloud();
            console.log('Local data migrated to cloud');
          }
          setMigrationDone(true);
        } catch (err) {
          console.error('Migration failed:', err);
          setMigrationDone(true); // Continue anyway
        }
      }
    }
    migrate();
  }, [isAuthenticated, migrationDone]);

  // Handle "Continue as Guest" from intro screen
  const handleGetStarted = async () => {
    await markIntroSeen();
    setIntroSeen(true);
    setIsGuestMode(true);
  };

  // Handle "Sign Up" from intro screen
  const handleSignUpFromIntro = async () => {
    await markIntroSeen();
    setIntroSeen(true);
    setShowAuth(true);
  };

  // Handle successful sign in
  const handleSignIn = async (email, password) => {
    await signIn(email, password);
    setShowAuth(false);
    setIsGuestMode(false);
  };

  // Handle successful sign up
  const handleSignUp = async (email, password) => {
    await signUp(email, password);
    setShowAuth(false);
    setIsGuestMode(false);
  };

  // Handle Google sign in
  const handleGoogleSignIn = async () => {
    await signInWithGoogle();
    setShowAuth(false);
    setIsGuestMode(false);
  };

  // Handle logout (from MultiScreenView)
  const handleLogout = async () => {
    await logOut();
    setMigrationDone(false);
    setShowAuth(true);
    setIsGuestMode(false);
  };

  // Handle "Sign In" button from MultiScreenView (guest wanting to sign in)
  const handleRequestSignIn = () => {
    setShowAuth(true);
    setIsGuestMode(false);
  };

  // Handle "Continue as Guest" from auth screen (go back to guest mode)
  const handleContinueAsGuest = () => {
    setShowAuth(false);
    setIsGuestMode(true);
  };

  // Loading state
  if (introSeen === null || authLoading) {
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        background: '#f5f6f8',
        color: '#6b7280'
      }}>
        Loading...
      </div>
    );
  }

  // Intro screen (first time users)
  if (!introSeen) {
    return (
      <IntroScreen
        onGetStarted={handleGetStarted}
        onSignUp={handleSignUpFromIntro}
        showAuthOptions={firebaseEnabled}
      />
    );
  }

  // Auth screen (if Firebase enabled and not in guest mode)
  if (firebaseEnabled && showAuth && !isAuthenticated && !isGuestMode) {
    return (
      <AuthScreen
        onSignIn={handleSignIn}
        onSignUp={handleSignUp}
        onGoogleSignIn={handleGoogleSignIn}
        onResetPassword={resetPassword}
        onContinueAsGuest={handleContinueAsGuest}
        error={authError}
        clearError={clearError}
        isLoading={authLoading}
      />
    );
  }

  // Main app (authenticated or guest mode)
  return (
    <MultiScreenView
      onLogout={isAuthenticated ? handleLogout : null}
      onRequestSignIn={!isAuthenticated && firebaseEnabled ? handleRequestSignIn : null}
      isAuthenticated={isAuthenticated}
      userEmail={user?.email}
    />
  );
}
