import React, { useState } from 'react';
import styles from './AuthScreen.module.css';
import logo from '../assets/icon-rounded.png';

/**
 * AuthScreen - Sign in / Sign up screen
 * Supports email/password and Google sign-in
 */
export default function AuthScreen({
  onSignIn,
  onSignUp,
  onGoogleSignIn,
  onResetPassword,
  onContinueAsGuest,
  error,
  clearError,
  isLoading
}) {
  const [mode, setMode] = useState('signin'); // 'signin', 'signup', 'reset'
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [localError, setLocalError] = useState('');
  const [resetSent, setResetSent] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLocalError('');
    clearError?.();

    if (mode === 'reset') {
      if (!email) {
        setLocalError('Please enter your email address.');
        return;
      }
      try {
        await onResetPassword(email);
        setResetSent(true);
      } catch (err) {
        // Error handled by hook
      }
      return;
    }

    if (!email || !password) {
      setLocalError('Please enter both email and password.');
      return;
    }

    if (mode === 'signup') {
      if (password !== confirmPassword) {
        setLocalError('Passwords do not match.');
        return;
      }
      if (password.length < 6) {
        setLocalError('Password must be at least 6 characters.');
        return;
      }
      try {
        await onSignUp(email, password);
      } catch (err) {
        // Error handled by hook
      }
    } else {
      try {
        await onSignIn(email, password);
      } catch (err) {
        // Error handled by hook
      }
    }
  };

  const handleGoogleSignIn = async () => {
    clearError?.();
    try {
      await onGoogleSignIn();
    } catch (err) {
      // Error handled by hook
    }
  };

  const switchMode = (newMode) => {
    setMode(newMode);
    setLocalError('');
    setResetSent(false);
    clearError?.();
  };

  const displayError = localError || error;

  return (
    <div className={styles.container}>
      <div className={styles.card}>
        <div className={styles.logo}>
          <img src={logo} alt="SEEN" className={styles.logoImage} />
        </div>

        {mode === 'reset' ? (
          // Password Reset Form
          <form onSubmit={handleSubmit} className={styles.form}>
            <h2>Reset Password</h2>

            {resetSent ? (
              <div className={styles.success}>
                Check your email for a password reset link.
              </div>
            ) : (
              <>
                <p className={styles.subtitle}>
                  Enter your email and we'll send you a reset link.
                </p>

                <div className={styles.inputGroup}>
                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    id="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="you@example.com"
                    autoComplete="email"
                    disabled={isLoading}
                  />
                </div>

                {displayError && (
                  <div className={styles.error}>{displayError}</div>
                )}

                <button
                  type="submit"
                  className={styles.primaryButton}
                  disabled={isLoading}
                >
                  {isLoading ? 'Sending...' : 'Send Reset Link'}
                </button>
              </>
            )}

            <button
              type="button"
              className={styles.linkButton}
              onClick={() => switchMode('signin')}
            >
              Back to Sign In
            </button>
          </form>
        ) : (
          // Sign In / Sign Up Form
          <form onSubmit={handleSubmit} className={styles.form}>
            <h2>{mode === 'signin' ? 'Sign In' : 'Create Account'}</h2>

            <div className={styles.inputGroup}>
              <label htmlFor="email">Email</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="you@example.com"
                autoComplete="email"
                disabled={isLoading}
              />
            </div>

            <div className={styles.inputGroup}>
              <label htmlFor="password">Password</label>
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder=""
                autoComplete={mode === 'signin' ? 'current-password' : 'new-password'}
                disabled={isLoading}
              />
            </div>

            {mode === 'signup' && (
              <div className={styles.inputGroup}>
                <label htmlFor="confirmPassword">Confirm Password</label>
                <input
                  type="password"
                  id="confirmPassword"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder=""
                  autoComplete="new-password"
                  disabled={isLoading}
                />
              </div>
            )}

            {displayError && (
              <div className={styles.error}>{displayError}</div>
            )}

            <button
              type="submit"
              className={styles.primaryButton}
              disabled={isLoading}
            >
              {isLoading
                ? 'Please wait...'
                : mode === 'signin'
                ? 'Sign In'
                : 'Create Account'}
            </button>

            {mode === 'signin' && (
              <button
                type="button"
                className={styles.linkButton}
                onClick={() => switchMode('reset')}
              >
                Forgot password?
              </button>
            )}

            <div className={styles.divider}>
              <span>or</span>
            </div>

            <button
              type="button"
              className={styles.googleButton}
              onClick={handleGoogleSignIn}
              disabled={isLoading}
            >
              <GoogleIcon />
              Continue with Google
            </button>

            {onContinueAsGuest && (
              <button
                type="button"
                className={styles.guestButton}
                onClick={onContinueAsGuest}
                disabled={isLoading}
              >
                Continue as Guest
              </button>
            )}

            <p className={styles.switchMode}>
              {mode === 'signin' ? (
                <>
                  Don't have an account?{' '}
                  <button type="button" onClick={() => switchMode('signup')}>
                    Sign up
                  </button>
                </>
              ) : (
                <>
                  Already have an account?{' '}
                  <button type="button" onClick={() => switchMode('signin')}>
                    Sign in
                  </button>
                </>
              )}
            </p>
          </form>
        )}

        <p className={styles.guestNote}>
          <strong>Guest mode:</strong> Your data stays on this device only.
          <br />
          <strong>Sign in:</strong> Sync your data across all your devices.
        </p>
      </div>
    </div>
  );
}

// Google icon SVG
function GoogleIcon() {
  return (
    <svg viewBox="0 0 24 24" width="18" height="18" className={styles.googleIcon}>
      <path
        fill="#4285F4"
        d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
      />
      <path
        fill="#34A853"
        d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
      />
      <path
        fill="#FBBC05"
        d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
      />
      <path
        fill="#EA4335"
        d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
      />
    </svg>
  );
}
