import React, { useRef, useEffect } from 'react';
import ScreenHeader from '../components/ScreenHeader';
import { useIsMobile } from '../hooks/useIsMobile';
import styles from './StoryScreen.module.css';

/**
 * Screen 1: Story (Narrative)
 * Patient info + main health narrative
 */
export default function StoryScreen({ patientData, visitData, updatePatient, updateVisit }) {
  const isMobile = useIsMobile();
  const textareaRef = useRef(null);

  // Auto-resize textarea as user types
  const autoResize = (e) => {
    const textarea = e?.target || textareaRef.current;
    if (textarea) {
      textarea.style.height = 'auto';
      textarea.style.height = textarea.scrollHeight + 'px';
    }
  };

  // Auto-resize on mount and when value changes
  useEffect(() => {
    if (textareaRef.current) {
      autoResize({ target: textareaRef.current });
    }
  }, [visitData?.symptoms]);

  return (
    <div className={styles.screen}>
      <ScreenHeader
        title="Your Story"
        subtitle="Share your health journey in your own words"
      />

      {/* Patient Info */}
      <div className={styles.inputGroup}>
        <label className={styles.inputLabel}>
          <span className={styles.labelText}>Name</span>
          <input
            type="text"
            className={styles.input}
            value={patientData?.name || ''}
            onChange={(e) => updatePatient({ name: e.target.value })}
            placeholder="Jane Doe"
          />
        </label>

        <label className={styles.inputLabel}>
          <span className={styles.labelText}>Date of Birth</span>
          <input
            type="text"
            className={styles.input}
            value={patientData?.dob || ''}
            onChange={(e) => updatePatient({ dob: e.target.value })}
            placeholder="04/21/1984"
          />
        </label>
      </div>

      {/* Main Story Textarea */}
      <div className={styles.storySection}>
        <label className={styles.textareaLabel}>
          <span className={styles.labelText}>Your Health Story</span>
          <span className={styles.helperText}>
            Describe what's been happening, what's hardest right now, and what you want help with
          </span>
          <textarea
            ref={textareaRef}
            className={styles.textarea}
            value={visitData?.symptoms || ''}
            onChange={(e) => {
              updateVisit({ symptoms: e.target.value });
              autoResize(e);
            }}
            onInput={autoResize}
            placeholder="In 2006, I experienced a severe case of Epstein-Barr virus that marked the beginning of a long decline in my health. Over the following years, I developed persistent fatigue, muscle weakness, and cognitive fog. I was later diagnosed with Myalgic Encephalomyelitis/Chronic Fatigue Syndrome (ME/CFS), along with dysautonomia and Type 2 diabetes. These conditions have progressively limited my mobility  I am now mostly bedridden and can walk only a few steps before becoming weak, dizzy, and short of breath. My daily functioning is severely restricted, and even small physical or mental efforts can trigger symptom flare-ups lasting days or weeks."
            rows={isMobile ? 8 : 12}
          />
        </label>
      </div>

      {/* Future: AI Enhancement Button (disabled for now) */}
      <div className={styles.aiSection}>
        <button
          type="button"
          className={styles.aiButton}
          disabled
          title="Coming soon: AI will help make your story clearer for doctors"
        >
           Make clearer for doctor (Coming soon)
        </button>
      </div>
    </div>
  );
}
