import React from 'react';
import AppButton from './AppButton';
import styles from './NavigationFooter.module.css';
import { useIsMobile } from '../hooks/useIsMobile';

/**
 * NavigationFooter - Back/Next navigation buttons
 * Sticky on mobile, static on tablet/desktop
 *
 * @param {Function} onBack - Callback for Back button
 * @param {Function} onNext - Callback for Next button
 * @param {boolean} isFirst - True if on first screen (hides Back button)
 * @param {boolean} isLast - True if on last screen (hides Next button)
 */
export default function NavigationFooter({ onBack, onNext, isFirst, isLast }) {
  const isMobile = useIsMobile();

  const footerClasses = [
    styles.footer,
    isMobile ? styles.footerMobile : styles.footerDesktop
  ].join(' ');

  return (
    <div className={footerClasses}>
      {!isFirst && (
        <AppButton
          variant="secondary"
          onClick={onBack}
          className={styles.backButton}
        >
           Back
        </AppButton>
      )}

      {!isLast && (
        <AppButton
          variant="primary"
          onClick={onNext}
          className={styles.nextButton}
          style={{ marginLeft: isFirst ? 'auto' : undefined }}
        >
          Next 
        </AppButton>
      )}
    </div>
  );
}
