import React, { useState } from 'react';
import styles from './CollapsibleSection.module.css';

/**
 * CollapsibleSection - Expandable/collapsible section with count badge
 *
 * @param {string} title - Section title
 * @param {number} count - Optional count to show in badge (e.g., "Medications (3)")
 * @param {boolean} defaultOpen - Whether section starts open (default: true)
 * @param {React.ReactNode} action - Optional action element (e.g., Remove button) shown on the right
 * @param {React.ReactNode} children - Section content
 */
export default function CollapsibleSection({
  title,
  count,
  defaultOpen = true,
  action,
  children
}) {
  const [isOpen, setIsOpen] = useState(defaultOpen);

  const toggleOpen = () => setIsOpen(!isOpen);

  const handleActionClick = (e) => {
    e.stopPropagation();
  };

  return (
    <div className={styles.section}>
      <button
        type="button"
        className={styles.header}
        onClick={toggleOpen}
        aria-expanded={isOpen}
      >
        <span className={styles.icon}>
          {isOpen ? '' : ''}
        </span>
        <span className={styles.title}>{title}</span>
        {count !== undefined && count > 0 && (
          <span className={styles.badge}>({count})</span>
        )}
        {action && (
          <span className={styles.action} onClick={handleActionClick}>
            {action}
          </span>
        )}
      </button>

      {isOpen && (
        <div className={styles.content}>
          {children}
        </div>
      )}
    </div>
  );
}
