import React from 'react';
import ScreenHeader from '../components/ScreenHeader';
import DiagnosisCard from '../components/DiagnosisCard';
import { ensureArrayField, updateArrayItem, addArrayItem, removeArrayItem, getEmptyTemplate } from '../utils/dataHelpers';
import styles from './DiagnosesScreen.module.css';

/**
 * Screen 2: Diagnoses
 * List of diagnosed conditions
 */
export default function DiagnosesScreen({ patientData, updatePatient }) {
  // Ensure diagnoses array exists
  const diagnoses = Array.isArray(patientData?.diagnoses)
    ? patientData.diagnoses
    : ensureArrayField('diagnoses');

  // Update a specific diagnosis field
  const handleUpdate = (index, field, value) => {
    const updated = updateArrayItem(diagnoses, index, field, value);
    updatePatient({ diagnoses: updated });
  };

  // Add new diagnosis
  const handleAdd = () => {
    const template = getEmptyTemplate('diagnoses');
    const updated = addArrayItem(diagnoses, template);
    updatePatient({ diagnoses: updated });
  };

  // Delete diagnosis (keeps at least one)
  const handleDelete = (index) => {
    const template = getEmptyTemplate('diagnoses');
    const updated = removeArrayItem(diagnoses, index, template);
    updatePatient({ diagnoses: updated });
  };

  return (
    <div className={styles.screen}>
      <ScreenHeader
        title="Diagnoses"
        subtitle="Conditions you've been diagnosed with"
      />

      <div className={styles.list}>
        {diagnoses.map((diagnosis, index) => (
          <DiagnosisCard
            key={index}
            diagnosis={diagnosis}
            index={index}
            onUpdate={(field, value) => handleUpdate(index, field, value)}
            onDelete={() => handleDelete(index)}
          />
        ))}
      </div>

      <button
        type="button"
        className={styles.addButton}
        onClick={handleAdd}
      >
        + Add Diagnosis
      </button>
    </div>
  );
}
