import React from 'react';
import ScreenHeader from '../components/ScreenHeader';
import CollapsibleSection from '../components/CollapsibleSection';
import { ensureArrayField, updateArrayItem, addArrayItem, removeArrayItem, getEmptyTemplate } from '../utils/dataHelpers';
import styles from './DoctorsScreen.module.css';

/**
 * Screen 5: Doctors & Specialists
 * List of healthcare providers
 */
export default function DoctorsScreen({ patientData, updatePatient }) {
  const doctors = Array.isArray(patientData?.doctors)
    ? patientData.doctors
    : ensureArrayField('doctors');

  return (
    <div className={styles.screen}>
      <ScreenHeader
        title="Doctors & Specialists"
        subtitle="Your healthcare team"
      />

      {doctors.map((doctor, index) => (
        <CollapsibleSection
          key={index}
          title={doctor.name || `Doctor ${index + 1}`}
          defaultOpen={true}
          action={
            <button
              type="button"
              className={styles.deleteButton}
              onClick={() => {
                const updated = removeArrayItem(doctors, index, getEmptyTemplate('doctors'));
                updatePatient({ doctors: updated });
              }}
            >
              Remove
            </button>
          }
        >
          <div className={styles.card}>
            <div className={styles.content}>
              <label className={styles.field}>
                <span className={styles.label}>Name</span>
                <input
                  type="text"
                  className={styles.input}
                  value={doctor.name || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(doctors, index, 'name', e.target.value);
                    updatePatient({ doctors: updated });
                  }}
                  placeholder="Dr. L. Patel"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Specialty</span>
                <input
                  type="text"
                  className={styles.input}
                  value={doctor.specialty || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(doctors, index, 'specialty', e.target.value);
                    updatePatient({ doctors: updated });
                  }}
                  placeholder="Neurology"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Contact Information</span>
                <input
                  type="text"
                  className={styles.input}
                  value={doctor.contact || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(doctors, index, 'contact', e.target.value);
                    updatePatient({ doctors: updated });
                  }}
                  placeholder="City General Hospital"
                />
              </label>
            </div>
          </div>
        </CollapsibleSection>
      ))}

      <button
        type="button"
        className={styles.addButton}
        onClick={() => {
          const updated = addArrayItem(doctors, getEmptyTemplate('doctors'));
          updatePatient({ doctors: updated });
        }}
      >
        + Add Doctor
      </button>
    </div>
  );
}
