import React from 'react';
import ScreenHeader from '../components/ScreenHeader';
import CollapsibleSection from '../components/CollapsibleSection';
import { ensureArrayField, updateArrayItem, addArrayItem, removeArrayItem, getEmptyTemplate, filterNonEmptyRows } from '../utils/dataHelpers';
import styles from './MedicalHistoryScreen.module.css';

/**
 * Screen 3: Medical History
 * Four collapsible sections: Hospitalizations, Procedures, Treatments, Tests & Imaging
 */
export default function MedicalHistoryScreen({ patientData, updatePatient }) {
  // Initialize arrays
  const hospitalizations = Array.isArray(patientData?.hospitalizations)
    ? patientData.hospitalizations
    : ensureArrayField('hospitalizations');

  const procedures = Array.isArray(patientData?.procedures)
    ? patientData.procedures
    : ensureArrayField('procedures');

  const treatments = Array.isArray(patientData?.treatments)
    ? patientData.treatments
    : ensureArrayField('treatments');

  const testsImaging = Array.isArray(patientData?.testsImaging)
    ? patientData.testsImaging
    : ensureArrayField('testsImaging');

  // Calculate counts (non-empty entries)
  const hospCount = filterNonEmptyRows(hospitalizations, ['why', 'when']).length;
  const procCount = filterNonEmptyRows(procedures, ['name', 'date']).length;
  const treatCount = filterNonEmptyRows(treatments, ['name']).length;
  const testsCount = filterNonEmptyRows(testsImaging, ['test']).length;

  return (
    <div className={styles.screen}>
      <ScreenHeader
        title="Medical History"
        subtitle="Past hospitalizations, procedures, treatments, and tests"
      />

      {/* Hospitalizations Section */}
      <CollapsibleSection title="Hospitalizations" count={hospCount} defaultOpen={false}>
        <div className={styles.sectionContent}>
          {hospitalizations.map((item, index) => (
            <div key={index} className={styles.itemCard}>
              <div className={styles.itemHeader}>
                <span className={styles.itemNumber}>Entry {index + 1}</span>
                <button
                  type="button"
                  className={styles.deleteButton}
                  onClick={() => {
                    const updated = removeArrayItem(
                      hospitalizations,
                      index,
                      getEmptyTemplate('hospitalizations')
                    );
                    updatePatient({ hospitalizations: updated });
                  }}
                >
                  Remove
                </button>
              </div>

              <label className={styles.field}>
                <span className={styles.label}>Reason</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.why || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(hospitalizations, index, 'why', e.target.value);
                    updatePatient({ hospitalizations: updated });
                  }}
                  placeholder="Severe dehydration and acute glucose crisis (diabetes)"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Date or Timeframe</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.when || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(hospitalizations, index, 'when', e.target.value);
                    updatePatient({ hospitalizations: updated });
                  }}
                  placeholder="March 2012"
                />
              </label>
            </div>
          ))}

          <button
            type="button"
            className={styles.addButton}
            onClick={() => {
              const updated = addArrayItem(hospitalizations, getEmptyTemplate('hospitalizations'));
              updatePatient({ hospitalizations: updated });
            }}
          >
            + Add Hospitalization
          </button>
        </div>
      </CollapsibleSection>

      {/* Procedures & Surgeries Section */}
      <CollapsibleSection title="Procedures & Surgeries" count={procCount} defaultOpen={false}>
        <div className={styles.sectionContent}>
          {procedures.map((item, index) => (
            <div key={index} className={styles.itemCard}>
              <div className={styles.itemHeader}>
                <span className={styles.itemNumber}>Entry {index + 1}</span>
                <button
                  type="button"
                  className={styles.deleteButton}
                  onClick={() => {
                    const updated = removeArrayItem(procedures, index, getEmptyTemplate('procedures'));
                    updatePatient({ procedures: updated });
                  }}
                >
                  Remove
                </button>
              </div>

              <label className={styles.field}>
                <span className={styles.label}>Procedure Name</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.name || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(procedures, index, 'name', e.target.value);
                    updatePatient({ procedures: updated });
                  }}
                  placeholder="Tilt table test"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Approximate Date</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.date || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(procedures, index, 'date', e.target.value);
                    updatePatient({ procedures: updated });
                  }}
                  placeholder="January 2014"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Notes</span>
                <textarea
                  className={styles.textarea}
                  value={item.notes || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(procedures, index, 'notes', e.target.value);
                    updatePatient({ procedures: updated });
                  }}
                  placeholder="Used to diagnose dysautonomia, showed abnormal heart rate response"
                  rows={2}
                />
              </label>
            </div>
          ))}

          <button
            type="button"
            className={styles.addButton}
            onClick={() => {
              const updated = addArrayItem(procedures, getEmptyTemplate('procedures'));
              updatePatient({ procedures: updated });
            }}
          >
            + Add Procedure
          </button>
        </div>
      </CollapsibleSection>

      {/* Treatments Section */}
      <CollapsibleSection title="Treatments" count={treatCount} defaultOpen={false}>
        <div className={styles.sectionContent}>
          {treatments.map((item, index) => (
            <div key={index} className={styles.itemCard}>
              <div className={styles.itemHeader}>
                <span className={styles.itemNumber}>Entry {index + 1}</span>
                <button
                  type="button"
                  className={styles.deleteButton}
                  onClick={() => {
                    const updated = removeArrayItem(treatments, index, getEmptyTemplate('treatments'));
                    updatePatient({ treatments: updated });
                  }}
                >
                  Remove
                </button>
              </div>

              <label className={styles.field}>
                <span className={styles.label}>Treatment Name</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.name || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(treatments, index, 'name', e.target.value);
                    updatePatient({ treatments: updated });
                  }}
                  placeholder="Graded activity pacing"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Timeframe</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.timeframe || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(treatments, index, 'timeframe', e.target.value);
                    updatePatient({ treatments: updated });
                  }}
                  placeholder="2015"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Effectiveness</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.effectiveness || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(treatments, index, 'effectiveness', e.target.value);
                    updatePatient({ treatments: updated });
                  }}
                  placeholder="Helps avoid post-exertional crashes"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Side Effects</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.sideEffects || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(treatments, index, 'sideEffects', e.target.value);
                    updatePatient({ treatments: updated });
                  }}
                  placeholder="None reported"
                />
              </label>
            </div>
          ))}

          <button
            type="button"
            className={styles.addButton}
            onClick={() => {
              const updated = addArrayItem(treatments, getEmptyTemplate('treatments'));
              updatePatient({ treatments: updated });
            }}
          >
            + Add Treatment
          </button>
        </div>
      </CollapsibleSection>

      {/* Tests & Imaging Section */}
      <CollapsibleSection title="Tests & Imaging" count={testsCount} defaultOpen={false}>
        <div className={styles.sectionContent}>
          {testsImaging.map((item, index) => (
            <div key={index} className={styles.itemCard}>
              <div className={styles.itemHeader}>
                <span className={styles.itemNumber}>Entry {index + 1}</span>
                <button
                  type="button"
                  className={styles.deleteButton}
                  onClick={() => {
                    const updated = removeArrayItem(testsImaging, index, getEmptyTemplate('testsImaging'));
                    updatePatient({ testsImaging: updated });
                  }}
                >
                  Remove
                </button>
              </div>

              <label className={styles.field}>
                <span className={styles.label}>Test Name</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.test || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(testsImaging, index, 'test', e.target.value);
                    updatePatient({ testsImaging: updated });
                  }}
                  placeholder="Brain MRI"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Key Finding</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.finding || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(testsImaging, index, 'finding', e.target.value);
                    updatePatient({ testsImaging: updated });
                  }}
                  placeholder="No structural abnormalities"
                />
              </label>

              <label className={styles.field}>
                <span className={styles.label}>Date</span>
                <input
                  type="text"
                  className={styles.input}
                  value={item.date || ''}
                  onChange={(e) => {
                    const updated = updateArrayItem(testsImaging, index, 'date', e.target.value);
                    updatePatient({ testsImaging: updated });
                  }}
                  placeholder="2016"
                />
              </label>
            </div>
          ))}

          <button
            type="button"
            className={styles.addButton}
            onClick={() => {
              const updated = addArrayItem(testsImaging, getEmptyTemplate('testsImaging'));
              updatePatient({ testsImaging: updated });
            }}
          >
            + Add Test/Imaging
          </button>
        </div>
      </CollapsibleSection>
    </div>
  );
}
