import React from 'react';
import { useScreenNavigation, SCREENS } from './hooks/useScreenNavigation';
import { usePatientData } from './hooks/usePatientData';
import NavigationHeader from './components/NavigationHeader';
import NavigationFooter from './components/NavigationFooter';
import StoryScreen from './screens/StoryScreen';
import DiagnosesScreen from './screens/DiagnosesScreen';
import MedicalHistoryScreen from './screens/MedicalHistoryScreen';
import MedicationsScreen from './screens/MedicationsScreen';
import DoctorsScreen from './screens/DoctorsScreen';
import ReviewScreen from './screens/ReviewScreen';
import PrintSummary from './PrintSummary';
import styles from './MultiScreenView.module.css';

/**
 * MultiScreenView - Main container for 6-screen medical form flow
 * Manages navigation, data persistence, and print functionality
 */
export default function MultiScreenView({ onLogout, onRequestSignIn }) {
  // Navigation state
  const navigation = useScreenNavigation();

  // Patient data via centralized hook (handles loading/saving)
  const {
    patientData: localPatient,
    visitData: localVisit,
    updatePatient,
    updateVisit,
    isLoading
  } = usePatientData();

  // Show loading state while data loads
  if (isLoading) {
    return (
      <div className={styles.container}>
        <div className={styles.loading}>Loading...</div>
      </div>
    );
  }

  // Render current screen based on navigation
  const renderScreen = () => {
    const commonProps = {
      patientData: localPatient,
      visitData: localVisit,
      updatePatient,
      updateVisit,
    };

    switch (navigation.currentScreen) {
      case SCREENS.STORY:
        return <StoryScreen {...commonProps} />;

      case SCREENS.DIAGNOSES:
        return <DiagnosesScreen {...commonProps} />;

      case SCREENS.HISTORY:
        return <MedicalHistoryScreen {...commonProps} />;

      case SCREENS.MEDICATIONS:
        return <MedicationsScreen {...commonProps} />;

      case SCREENS.DOCTORS:
        return <DoctorsScreen {...commonProps} />;

      case SCREENS.REVIEW:
        return (
          <ReviewScreen
            {...commonProps}
            navigation={navigation}
            onLogout={onLogout}
            onRequestSignIn={onRequestSignIn}
          />
        );

      default:
        return <StoryScreen {...commonProps} />;
    }
  };

  return (
    <div className={styles.container}>
      {/* Screen-only view (visible in browser) */}
      <div className="screen-only">
        <NavigationHeader
          screenNumber={navigation.screenNumber}
          totalScreens={navigation.totalScreens}
          screenName={navigation.screenName}
        />

        <div className={styles.content}>
          {renderScreen()}
        </div>

        <NavigationFooter
          onBack={navigation.goBack}
          onNext={navigation.goNext}
          isFirst={navigation.isFirst}
          isLast={navigation.isLast}
        />
      </div>

      {/* Print-only view (hidden in browser, visible when printing) */}
      <div className="print-only">
        <PrintSummary
          patientPermanent={localPatient}
          visitData={localVisit}
        />
      </div>
    </div>
  );
}
